@font-face {
  font-family: 'GangwonEdu_OTFBoldA';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEdu_OTFBoldA.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'OTWelcomeRA';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2110@1.0/OTWelcomeRA.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'TheJamsil5Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap');
@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);

.notosanskr * { 
 font-family: 'Noto Sans KR', sans-serif;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  color: #222;
  
  /* font-family: 'GangwonEdu_OTFBoldA';
  
   font-family: 'Noto Sans KR', sans-serif; */
   font-family: 'OTWelcomeRA';
  

}
a{
  text-decoration: none;
  color: #222;
}
ol,ul,li{
  list-style: none;
 margin: 0;
 padding: 0; 
}
.flex_{
  display: flex;
}
.nav {
  margin: 10px 30px 0 10px;
}

.navbar {
  width: 100%;
  position: sticky;
  top: 0px;
  z-index: 10;
  background-color: #fff;
 
}

.navbar-nav {
  width: 400px;
  justify-content: space-between;
  
}
.navbar-nav>a{
  color: #999999;
  display: block;
  height: 30px;

}

.navbar-nav>a:hover{
  color: #222;
  text-decoration: underline;
  text-underline-position: under;
  
}


.icon {
  width: 165px;
}

.navbar-toggler {
  background-color: #222;

}

.nav-link{
  padding: 0!important;
  line-height: 37px;
 
}
.nav-link:nth-of-type(3)>img{
  margin-bottom: 20px!important;
  
}
.navbar-toggler>span {
  color: #222;
 
}
/* .logo{
  width: 80%;
} */
.logo img {
  width: 250px;
  height: 100%;
}

.container {
  margin: 0 30px 0 0;
  max-width: 100%;
}

.nav-link {
  padding: 5px;
  color: #222;

}

.nav_menu {
  font-size: 18px;
  width: 100px;
  text-align: center;
}

.nav_right {
  width: 50px;
}

.slider {
  position: relative;
  margin-bottom: 50px;
}

.slider>img {
  width: 100%;
  height: 80vh;

}

.slider>.imgtext {
  position: absolute;
  z-index: 12;
  left: 0;
  width: 100%;
  height: 50vh;
  top: 80px;
}

.title-box {
  width:1500px;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: self-end;
  font-family: 'GangwonEdu_OTFBoldA';
  font-size: 20px;
  margin: 0 auto;
  border-bottom: 0.5px solid #eee;
}
.swiper{
  width: 80%;
  height: 650px;
}
.swiper-img-box>img{
  width: 100%;
  height: 430px;

} 

.swiper-box>.swiper-wrapper{
  width: 100%;
  height: 100%;
 margin: 0 auto;
}
.swiper-button-prev, .swiper-button-next{
  position: absolute;
  top: var(--swiper-navigation-top-offset,35%)!important;
  color: #eee!important;
}
.swiper-button-prev:hover:after ,.swiper-button-next:hover:after{
  color: #464646;
}



.Main-box{
  margin:50px 0;

}
.Main-box>h3{
  line-height: 123px;
margin: 0 auto;
  width:1500px;
  height: 151px;

}
.swiper-img-box>h5{
   height: 60px;
   margin-top: 30px;

 }
 .swiper-img-box>p{
  color:#222
 }

.Pro-item-box{
  width: 1500px;
  margin: 0 auto;
  padding: 0;
}

.benner{
  width: 100%;
  margin-top: 70px;
}
.benner>img{
  width: 50%;
}
.Title2{
  width: 1500px;
  height: 200px;
  margin: 0 auto;
;
}
.Title2>h3{
  height: 50px;
  line-height: 220px
}
.trand{
  height: 600px;
}
.trand img{
  height: 420px;
}

.trand h5{
  height: 40px;
  line-height: 50px;
}
.trand  p{
  color: #222;
}

.bg_t{
  background-color: #eee;
  width: 100%;
  height: 500px;
  padding: 50px;
  margin-top: 30px;
}
.reco_box{
  width: 1200px;
  height: 440px;
  margin: 0 auto;
  
}
.reco_img_box .reco_img{
  width: 280px;
  height: 300px;
  transition: all .5s;
  position: relative;
}
.reco-img-text{
  position: relative;
  overflow: hidden;
}


.reco_box .reco_img_box .reco_img{
  position: relative;
  height: 280px;
  overflow: hidden;
}
.reco-text{
  position: absolute;
  top: 0;
  background-color: #2222224d;
  display: block;
  width: 280px;
  height: 280px;
  font-size: 25px;
  font-weight: bold;
  color: #eee;
  padding: 120px 0;
  text-align: center;
  font-style: italic;
  transition: all .5s;
  cursor: pointer;
}
.reco-img-text:hover .reco_img{
  transition: all .5s;
  transform: scale(1.2);
}
.reco-img-text:hover .reco-text{
  transition: all .5s;
  transform: scale(1.1);
}

.reco_box .reco_img_box> h5, .reco_box{
  line-height: 50px;
  color: #222;
  font-size: 15px;
}

.reco_img_box> h6{
  color: #222;
  font-size: 13px;

}
.reco_box .reco_img_box> a{
  text-decoration: none;
  font-size: 13px;
  color: #222;
  display: block;
  width: 83px;
  height: 35px;
  border-bottom: 0.5px solid #222;
}

.re-box>h3{
  width: 1500px;
  height: 200px;
  margin: 0 auto;
  line-height: 220px;
}
.rev-item-box{
  width:1500px;
  height: 500px;

}

.re-img-box{
  height: 450px!important;
  
}

.re-img-box>img{
  position: relative;
}
.re-img-box>a{
  position: absolute;
  bottom: 98px;
  right: 14px;
  color: #222;
 
  text-decoration: none;
  font-size: 25px;
}
.re-box .swiper-slide-active img{

  height: 420px;


transition: all .8s;
}
.re-box .swiper-slide-active .hart{
  bottom: 30px;
  transition: all .8s;
}
.re-img-box> img{
  width: 290px;
  height: 350px;
}
.re-img-box>h5{
  height: 50px;
  margin-top: 15px;
  font-size: 16px;
  margin-left: 5px;
}

.swiper-slide-shadow-left, .swiper-slide-shadow-left{
  width: 500px;
  height: 400px;
}

.last-benner{
  width: 1500px;
  height: 1120px;
  margin: 0 auto;
  padding-top: 60px;

}
.flower-benner{
  display: block;
  width: 1500px;
  height: 450px;
  margin: 50px 0 30px 0;
 
}
.benner1{
  position: relative;
}
.benner2{
  position: relative;
}

.last-benner button{
  position: absolute;
  width: 200px;
  height: 50px;
  top:50%;
  left: 50%;
 transform: translate(-50%,30%);
  border:.5px solid #222;
  background-color: transparent;
  padding-top: 8px;
}
hr{
  width: 100%;
}

.footer-box{
  width: 1300px;
  height: 500px;
  margin: 0 auto;
  padding: 30px 0;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.footer-text-box{
  width: 1000px;

}

.footer-text-box>h2{
  color:#a3a2a2;
  margin: 30px 0;
}
.footer-text-box>p{
  font-size: 12px;
  margin: 25px 0;
  color:#a3a2a2;
}
.footer-text-box>span{
  font-size: 13px;
}
.footer-text-box>span>a{
  display: inline-block;

}
.footer-text-box>span>a:nth-of-type(1){
  width: 85px;
}
.footer-text-box>span>a:nth-of-type(2){
  width: 145px;
  border-left: .5px solid #a3a2a2;
}
.footer-text-box>span>a:nth-of-type(3){
  width: 85px;
}
.infor1{
  height: 200px;
  
}

.infor-ul{
  width: 500px;
  height: 30px;
  justify-content: space-around
}
.infor-ul>li>ul{
  height: 60px;

}
.infor-ul li{
  height: 45px;
  line-height: 45px;
  font-size: 15px;
}
.infor-ul>li>ul>li{
  color: #a3a2a2;
}
.infor-ul>li>ul>li>a{
  display: block;
  color: #a3a2a2;
  width: 100%;
  height: 100;
  font-size: 14px;
}
.SNS{
  width: 100%;
  height: 50px;
  margin-top: 100px;
  justify-content: flex-end;
  margin-right: 0;

}
.SNS_img{
  width:200px;
  height: 100%;
  justify-content: space-around;
}
.btn-box{
  position:fixed;
  z-index: 100;
  right:30px;
  bottom: 100px;
  width: 50px;
  height: 110px;
  flex-direction: column;
  justify-content: space-between;
}
.kakao{
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;

}
.kakao>img{
  display: block;
  width: 50px;
  height: 50px;
 border-radius: 50%;
}
.top{
  display: block;
  text-align: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border:.5px solid  #999999;
  font-size: 26px;
  color: #9c9c9c;
  line-height: 60px;
  font-weight:100;
  background-color: transparent;
}






